import React from 'react'
import styled, {createGlobalStyle} from 'styled-components'
import { Link } from 'gatsby'
import backgroundImage from '../images/uki.jpg'

const GlobalStyle = createGlobalStyle`
  body, h1 {
    padding: 0;
    margin: 0;
  }

  *, *::before, *::after {
    box-sizing: border-box;
  }
`;

const StyledWrapper = styled.div`
  width: 100%;
  height: 100vh;
  background: url(${backgroundImage}) no-repeat center/cover;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: top;
  padding: 50px;
  font-family: 'Arial', sans-serif;
`;

const HeaderItem = styled.h1`
  color: rgba(247, 191, 94, 0.9);
  text-shadow: 1px 1px 5px rgba(68, 68, 68, 0.2);
  font-size: 5rem;
  font-weight: 700;
`;

const CiteItem = styled.q`
  width: 400px;
  margin: 50px 0 100px 0;
  color: rgb(255, 255, 255);
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.3);
  font-size: 2.5rem;
  font-weight: 700;
  text-align: right;
  line-height: 1.5;
`;

const LinkItem = styled.a`
a {
  text-decoration: none;
  color: rgb(247, 191, 94);
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.3);
  font-size: 2rem;
  font-weight: 700;
}

a:hover {
  color: rgb(255, 255, 255);
}
`;

const IndexPage = () => (
  <>
    <GlobalStyle/>
    <StyledWrapper>
      <HeaderItem>uki</HeaderItem>
      <CiteItem>Od królika do papugi jeden krok</CiteItem>
      <LinkItem><Link to={'/'}>Powrót</Link></LinkItem>
    </StyledWrapper>
  </>
)

export default IndexPage